"use client";

import { useSearchParams } from "next/navigation";
import { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";

interface Precio {
  id_precio: number;
  precio_producto: number;
  id_producto: number;
  id_establecimiento: number;
  establecimientos: {
    id_establecimiento: number;
    nombre_establecimiento: string;
  };
}

interface PrecioProps {
  productoId: number;
  establecimiento: string;
  precio: number;
}

interface ProductsProps {
  precios: PrecioProps[];
}

const ProductsComponent = ({ precios = [] }: ProductsProps) => {
  // Estado para rastrear la cantidad de cada producto
  const [cantidades, setCantidades] = useState<{ [key: string]: number }>(
    precios.reduce((acc, precio, index) => {
      acc[`${precio.productoId}-${index}`] = 1;
      return acc;
    }, {} as { [key: string]: number })
  );

  // Función para incrementar la cantidad
  const incrementarCantidad = (productoId: number, index: number) => {
    const key = `${productoId}-${index}`;
    setCantidades({
      ...cantidades,
      [key]: (cantidades[key] || 1) + 1,
    });
  };

  // Función para decrementar la cantidad (mínimo 1)
  const decrementarCantidad = (productoId: number, index: number) => {
    const key = `${productoId}-${index}`;
    if (cantidades[key] > 1) {
      setCantidades({
        ...cantidades,
        [key]: cantidades[key] - 1,
      });
    }
  };

  // Función para agregar a la lista
  const agregarALista = (
    productoId: number,
    establecimiento: string,
    precio: number,
    cantidad: number
  ) => {
    // Aquí puedes implementar la lógica para agregar a la lista de compras
    console.log("Agregado a la lista:", {
      productoId,
      establecimiento,
      precio,
      cantidad,
      total: precio * cantidad,
    });
    alert(
      `Se agregó ${cantidad} ${
        cantidad > 1 ? "unidades" : "unidad"
      } al carrito de compras`
    );
  };

  if (precios.length === 0) {
    return (
      <div className="text-center p-4">
        No se encontraron precios para este producto
      </div>
    );
  }

  return (
    <div className="container mx-auto py-8">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {precios.map((precio, index) => {
          const key = `${precio.productoId}-${index}`;
          const cantidad = cantidades[key] || 1;

          return (
            <div
              key={index}
              className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow duration-300"
            >
              <div className="h-48 bg-gray-200 relative">
                {/* Placeholder para imagen del establecimiento */}
                <div className="w-full h-full flex items-center justify-center bg-gray-100 text-gray-400">
                  <span>{precio.establecimiento}</span>
                </div>
              </div>
              <div className="p-6">
                <h2 className="font-bold text-xl mb-2">
                  {precio.establecimiento}
                </h2>
                <p className="text-gray-700 text-lg font-semibold mb-4">
                  ${precio.precio.toFixed(2)}
                </p>

                {/* Controles de cantidad */}
                <div className="flex items-center mb-4">
                  <span className="mr-3 text-sm font-medium">Cantidad:</span>
                  <div className="flex items-center border border-gray-300 rounded">
                    <button
                      onClick={() =>
                        decrementarCantidad(precio.productoId, index)
                      }
                      className="px-3 py-1 bg-gray-100 hover:bg-gray-200 text-gray-700"
                    >
                      -
                    </button>
                    <span className="px-4 py-1">{cantidad}</span>
                    <button
                      onClick={() =>
                        incrementarCantidad(precio.productoId, index)
                      }
                      className="px-3 py-1 bg-gray-100 hover:bg-gray-200 text-gray-700"
                    >
                      +
                    </button>
                  </div>
                </div>

                {/* Total */}
                <p className="text-sm mb-4">
                  Total:{" "}
                  <span className="font-bold">
                    ${(precio.precio * cantidad).toFixed(2)}
                  </span>
                </p>

                {/* Botón para agregar a la lista */}
                <button
                  onClick={() =>
                    agregarALista(
                      precio.productoId,
                      precio.establecimiento,
                      precio.precio,
                      cantidad
                    )
                  }
                  className="w-full bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700 transition-colors flex items-center justify-center"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-5 w-5 mr-2"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
                    />
                  </svg>
                  Agregar a la lista
                </button>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

// Remove the Products default export since we're exporting Page as default
export default function Page() {
  const searchParams = useSearchParams();
  const nombreProducto = searchParams.get("nombre") || "Producto";
  const [precios, setPrecios] = useState<
    Array<{
      productoId: number;
      establecimiento: string;
      precio: number;
    }>
  >([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchPreciosProducto() {
      try {
        // 1. Obtener el ID del producto basado en el nombre
        const { data: productos, error: errorProductos } = await supabase
          .from("productos")
          .select("id_producto")
          .eq("nombre_producto", nombreProducto)
          .limit(1);

        if (errorProductos) throw errorProductos;
        if (!productos || productos.length === 0) {
          setError("No se encontró el producto");
          setLoading(false);
          return;
        }

        const productoId = productos[0].id_producto;

        // 2. Obtener los precios para este producto junto con información del establecimiento
        const { data: preciosData, error: errorPrecios } = await supabase
          .from("precios")
          .select(
            `
            id_precio, 
            precio_producto, 
            id_producto, 
            establecimientos:establecimientos(
              id_establecimiento,
              nombre_establecimiento
            )
          `
          )
          .eq("id_producto", productoId);

        if (errorPrecios) throw errorPrecios;

        console.log("Datos de precios recibidos:", preciosData);

        // 3. Formatear los datos correctamente según la estructura de la respuesta
        const preciosFormateados = preciosData.map((item) => {
          // Acceder correctamente al nombre del establecimiento según la estructura real de los datos
          let nombreEstablecimiento = "Establecimiento desconocido";

          // Adaptamos esto según cómo Supabase devuelve los datos relacionados
          // Podrían ser cualquiera de estos formatos:
          if (
            item.establecimientos &&
            typeof item.establecimientos === "object"
          ) {
            if (Array.isArray(item.establecimientos)) {
              nombreEstablecimiento =
                item.establecimientos[0]?.nombre_establecimiento ||
                nombreEstablecimiento;
            } else {
              nombreEstablecimiento =
                item.establecimientos.nombre_establecimiento ||
                nombreEstablecimiento;
            }
          }

          return {
            productoId: item.id_producto,
            establecimiento: nombreEstablecimiento,
            precio: item.precio_producto,
          };
        });

        console.log("Precios formateados:", preciosFormateados);
        setPrecios(preciosFormateados);
      } catch (error) {
        console.error("Error al cargar precios:", error);
        setError("Error al cargar información de precios");
      } finally {
        setLoading(false);
      }
    }

    fetchPreciosProducto();
  }, [nombreProducto]);

  return (
    <>
      <div className="flex justify-center items-center mt-10">
        <h1 className="text-6xl">{nombreProducto}</h1>
      </div>

      {loading ? (
        <div className="flex justify-center items-center mt-10">
          Cargando información del producto...
        </div>
      ) : error ? (
        <div className="flex justify-center items-center mt-10 text-red-600">
          {error}
        </div>
      ) : (
        <div className="flex justify-center items-center">
          <ProductsComponent precios={precios} />
        </div>
      )}
    </>
  );
}
